import React from 'react';
import dummy from '../data/fruit.json';

function Fruit(props) {
  return (
    <>
      <h3>Fruits json 데이터 출력하기</h3>
      <ul>
        {dummy.map(fruit => (
          <li key={fruit.num}>
            num : {fruit.num}&nbsp;
            name : {fruit.name}&nbsp;
            price : {fruit.price}&nbsp;
            color : {fruit.color}&nbsp;
            country : {fruit.country}
          </li>
        ))}
      </ul>
    </>
  );
}

export default Fruit;