import React from 'react';
import dummy from '../data/orders.json';

function Orders(props) {
  return (
    <>
      <h3>Orders</h3>
      <ul>
        {dummy.map(order => (
          <li>
            {order.o_day}&nbsp;
            {order.c_name}&nbsp;
            {order.g_code}&nbsp;
            {order.o_num}
          </li>
        ))}
      </ul>
    </>
  );
}

export default Orders;