import React, { useState } from 'react';
import './css/Tabmnu.css';

const Tabmnu = () => {
  // const [변수명, 함수명] = useState();
  const [menuNum, setMenuNum] = useState(1);

  // 객체명
  const tabMenuToggle = (e) => { // 메뉴 클릭시 호출되는 함수
    setMenuNum(e); // 상태값을 변화시키기 위한 state 함수를 호출한다.
  }
  
  return (
    <>
      <h3>Tabmnu 구현하기</h3>
      <nav className='menu_list'>
        <ul>
          {/* 삼항조건 연산자 => 조건식?참인값:거짓인값; 
            사용자가 li 태그를 클릭하면 tabMenuToggle 함수가 호출되면서 괄호 안에 있는 숫자를 상태값 변수(menuNum)에 적용하여 상태값 변수(menuNum)와 숫자가 일치하면 act 서식을 적용한다.
          */}
          <li onClick={() => tabMenuToggle(1)} className={menuNum === 1 ? 'act' : ''}>menu1</li>
          <li onClick={() => tabMenuToggle(2)} className={menuNum === 2 ? 'act' : ''}>menu2</li>
          <li onClick={() => tabMenuToggle(3)} className={menuNum === 3 ? 'act' : ''}>menu3</li>
          <li onClick={() => tabMenuToggle(4)} className={menuNum === 4 ? 'act' : ''}>menu4</li>
          {/* 리액트 클릭 이벤트 : onClick={객체명} */}
        </ul>
      </nav>

      <div className='tab_content'>
        {/* 내용 1, 2, 3, 4 */}
        {/* 삼항 조건 연산자를 활용하여 위에서 나온 상태값 변수 menuNum값을 아래 대입하여 해당하는 콘텐츠가 보여질 수 있도록 한다. */}
        {menuNum === 1 ? 
          (<div className='content'>내용1</div>) : menuNum === 2 ? 
          (<div className='content'>내용2</div>) : menuNum === 3 ? 
          (<div className='content'>내용3</div>) :
          (<div className='content'>내용4</div>)
        }
      </div>
    </>
  );
}

export default Tabmnu;