import React, {useState, useEffect} from 'react';

function UseEffect3(props) {
  const [width, setWidth] = useState(window.innerWidth);
  // innerWidth : 윈도우 화면의 패딩값을 포함한 너비를 말함.
  // outerWidth : 윈도우 화면의 마진값을 포함한 너비를 말함.
  const [height, setHeight] = useState(window.innerHeight);

  useEffect(() => {
    const handleResize = () => {
      setWidth(window.innerWidth);
      setHeight(window.innerHeight);
    }

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    }
  }, []);
  
  // 높이 구하기

  return (
    <>
      <h3>useEffect 예제 3. 윈도우 리사이즈 이벤트 감지하기</h3>
      <p>브라우저의 가로 크기를 조절하여 값 확인하기</p>
      <p>현재창 너비 : {width}px 입니다.</p>
      <p>현재창의 높이는 : {height}px 입니다.</p>
    </>
  );
}

export default UseEffect3;