import React, { useState } from 'react';

function Form2(props) {
  // 변수 선언
  let header = '';

  // 상태관리값 초기화
  const [name, setName] = useState(''); //(초기값 없다)
  const [pw_name, setPw] = useState('');

  // nameChange 함수 작성
  const nameChange = (e) => { //이름을 저장하기 위한 변수
    setName(e.target.value);
  }

  const pwChange = (e) => { //패스월드를 저장하기 위한 변수
    setPw(e.target.value);
  }

  const onDataReset = (e) => { //데이터를 삭제하기 위한 변수
    setName('');
    setPw('');
  }

  // 조건식을 사용하여 값이 있으면 출력, 그렇지 않으면 공백
  if(name) { // 조건식이 참이면
    header = <p>사용자가 입력한 이름은 {name} 이고, 패스워드는 {pw_name} 입니다.</p>
  } else { // 거짓일 경우
    header = '';
  }

  return (
    <>
      <h4>2. if문으로 조건식 작성하기</h4>
      {header}

      <form>
        <label for="id_name">ID : </label>
        <input 
          type='text' 
          name='name' 
          onChange={nameChange} 
          id='id_name' 
          value={name}
        />

        <label for="pw_name">PW : </label>
        <input 
          type='text' 
          id='pw_name'
          name='pw_name'
          onChange={pwChange}
          value={pw_name}
        />

        <p>
          <button type="button" onClick={onDataReset}>데이터 입력 초기화</button>
          {/* HTML 이벤트 설정 : <button onclick='onDataReset();'></button> */}
        </p>
      </form>
    </>
  );
}

export default Form2;