import React, { useState } from 'react';

// function Count2(props) {
const Count2 = (props) => {
  // 변수 초기값 지정
  const [num, setNum] = useState(1); //초기값 1

  // 1씩 더하기 함수
  const onPlus1 = () => {
    setNum(num + 1);
  }

  // 1씩 감소하는 함수
  const onMinus1 = () => {
    if(num === 1) {
      setNum(1);
    } else {
      setNum(num - 1);
    }
  }

  /*
    == : 변수값이 일치하는지 여부
    === : 변수값, 데이터 형식이 일치하는지 여부
    (경고사항 - 권고)
  */

  return (
    <>
      {/* 함수형 콤포넌트 */}

      <p>+, - 버튼을 클릭하면 숫자 증감(증가, 감소)하는 state 값을 작성하기</p>

      <div>숫자 출력되는 곳 : <span className="num">{num}</span></div>
      <input type="button" value="+" className="btn" onClick={onPlus1} />
      <input type="button" value="-" className="btn" onClick={onMinus1} />
    </>
  );
}

export default Count2;