import React, {useState} from 'react'; //상태 변환값 useState 추가

function Form3(props) {
  // 1. 상태값을 저장하기 위한 변수
  const [name, setName] = useState(''); // 이름 저장 //name: 변수명. 상태값
  const [result, setResult] = useState(''); // 전송 버튼 클릭시 내용 저장 //result: 상태변수

  // 2. nameChange 함수
  const nameChange = (e) => {
    setName(e.target.value); //사용자 입력값을 setName에 저장
  }

  // 3. 전송버튼 클릭시 실행하는 함수
  const dataSubmit = (e) => {
    e.preventDefault(); // 새로고침 방지 (JS, jQuery와 동일)
    setResult(`입력하신 이름은 : ${name} 입니다.`);
  }

  return (
    <>
      <form onSubmit={dataSubmit}> {/*React는 onSubmit*/}
        <h4>3. onSubmit(전송버튼)으로 입력한 내용 전송하기</h4>
        <p>사용자가 입력한 이름은 <strong>{name}</strong> 입니다.</p>
        <p id="result">{result && <strong>{result}</strong>}</p>

        <input type='text' name='name' onChange={nameChange} />
        <button type='submit'>전송하기</button> {/*HTML : form의 get,post방식의 action으로*/}
      </form>
    </>
  );
}

export default Form3;