import React from 'react';

function Class0(props) {
  return (
    <>
      <h3>Node.js 설치와 리액트 프로젝트 실행방법</h3>
      <ul>
        <li>Node JS 사이트에 방문하여 LTS&#40;안정화&#41; 버전을 다운로드하여 설치</li>
        <li>내 컴퓨터 &#62; 시작 &#62; 실행 &#62; cmd&#40;명령 프롬프트&#41; 실행</li>
        <li>node, npm 버전확인 : node -v, npm -v</li>
        <li>프로젝트 설치하고자 하는 폴더로 이동 : cd 폴더명</li>
        <li>npx 설치&#40;execute npm package binaries&#41; : npm i npx -g</li>
        <li>npx 버전 확인 : npx -v</li>
      </ul>

      <h3>2. VS Code에서 CMD 등록하여 리액트 프로젝트 설치하기</h3>
      <ul>
        <li>상단 메뉴에서 '터미널' 메뉴 클릭</li>
        <li>새 터미널 생성되면 오른쪽 메뉴 '+' 옆 화살표 클릭하여 '기본 프로필선택'하면 VS Code에서 항상 cmd 창이 나오게 된다.</li>
        <li>드라이브명을 전환하여 프로젝트를 설치할 드라이브&#40;폴더&#41;로 이동한다.</li>
        <li>cmd 창에서 npx create-react-app 프로젝트명&#40;폴더명&#41;</li>
        <li>cmd 창에서 npx create-react-app kdt =&#62; 프로젝트 설치함. 에러가 나오면 다시 재설치하거나 또는 npx i 명령어로 미설치된 파일 재설치한다.</li>
      </ul>

      <h3>3. 리액트 프로젝트 실행</h3>
      <p>리액트 프로젝트 폴더가 생성되고, 설치가 완료되면 'npm run start' or 'npm start' 명령어로 프로젝트를 실행한다.</p>
      
      <h3>4. 리액트 프로젝트 종료</h3>
      <p>프로젝트를 종료하고자 할 때는 브라우저 창만 종료하지 말고, VS Code 'cmd' 창에서 반드시 'ctrl + C' 키를 눌러서 안정적으로 종료를 한다.</p>

      <h3 className="act">프로젝트 폴더구조 알아보기</h3>
      <ul>
        <li>[node_modules] : babel 및 webpack을 통해 리액트를 실행시켜주는 파일들을 모아놓은 폴더이다. 백업하지 않아도 되며 나중에 npm i를 통해 다시 설치 가능.</li>
        <li>[public] : babel이 컴파일하여 변환된 문서 index.html을 저장하는 폴더이며, images, src 폴더를 웹 사이트에 업로드한다.</li>
        <li>[src] : 리액트로 개발하는 모든 대부분의 파일이 저장되는 폴더</li>
        <li>index.js : 리액트 진입하기 위한 파일로서 첫 화면 문서.</li>
        <li>App.js : 콤포넌트 문서들을 모아놓은 파일</li>
        <li>[build] : 리액트 프로젝트를 완성한 후 웹 서버에 업로드하고자 할 때 생성되는 폴더이며 'npm run build' 명령어를 통해 실행할 수 있다.</li>
      </ul>

      <h3>1. public 폴더의 index.html</h3>
      <p>index.html 문서의 div id='root'는 실제 index.js로부터 렌더링된 내용이 삽입되는 영역이다.</p>

      <h3>2. src 폴더의 index.js</h3>
      <p>App.js 파일로부터 내용을 불러온 자식요소 콤포넌트를 삽입하기 위한 부모 콤포넌트 문서이다.</p>
      <p>import React from 'react';</p>
      <p>import ReactDOM from 'react-dom';</p>
      <p>리액트를 구현할 수 있는 플러그인을 연결한 내용이며, js파일을 불러오는 것이기 때문에 import라는 키워드를 사용(ES6 문법)하였으며, 리액트에서는 자바스크립트 확장자 .js를 생략한다.</p>
      <p>import 불러올 무언가의 이름 from '파일경로';</p>
      <p>css파일은 import로 문서서식을 불러오고자 할 때 확장자 '.css'를 작성한다.</p>
      <p>import './index.css';  //확장자까지 모두 작성해야...</p>

      <h3>3. index.js 파일의 root.render() 함수</h3>
      <pre>
        root.render(
          출력할 내용이 들어가는 곳
        )
      </pre>
      <p>render : html 문서 내용을 웹 페이지에 렌더링한다.</p>
      <p>document.getElementById('root')를 통해 아이디가 root인 곳에 App을 렌더링하겠다는 의미</p>
      <p>실제로 DOM으로 가상 돔을 불러오겠다는 함수의 의미</p>
      <p>여기서 'App /'는 App.js의 콤포넌트를 가져오겠다는 의미</p>

      <h3>4. 완성된 프로젝트를 빌드하여 웹 배포하기</h3>
      <p>vs code 하단 cmd 창에서 npm run build 작성하여 실행</p>
      <p>build 폴더가 생성되면 모든 파일이 들어가 있음(index.html, css, js, images, ...)</p>
      <p>index.html 파일을 선택하여 실행함. 페이지 나오는지 확인</p>

      <h3>5. build 폴더의 index.html 실행 시 아무것도 나오지 않는 경우</h3>
      <p>아래 package.json 파일 내용을 수정하고 'npm run build'를 다시 실행함.</p>
      <pre>
        "developent" : [
        "last 1 chrome version",
        "last 1 firefox version", 
        "last 1 safari version"
        ]
      "homepage" : "./"
      </pre>
    </>
  );
}

export default Class0;