import React, {useState, useEffect} from 'react';
import dummy from '../data/book_store.json';

function Bookstore(props) {
  const [books, setBooks] = useState([]); // 상태값 초기화

  useEffect(() => { // 비동기로 데이터 불러오기
    // fetch('/data/bookstore.json') //public폴더에 data폴더가 있어야. // 안 될 때 아래 방법으로
    fetch(`${process.env.PUBLIC_URL}/data/bookstore.json`) //위의 경로 인식 안 될 경우 강제 연결
    .then(response => response.json())
    .then(json => setBooks(json))
    .catch(err => console.error('데이터 불러오기 실패', err)) //데이터 불러오지 않을 경우 오류메시지 출력
  }, []);

  return (
    <>
      <h3>Book_store json 데이터 출력하기</h3>

      <p>1. 파일이 작은 경우 import 방식으로 데이터 출력(정적)</p>
      <ul>
        {dummy.map(book => (
          <li key={book.id}>
            🟡NUM : {book.num}&nbsp;
            👩NAME : {book.name}&nbsp;
            🗺지역1 : {book.area1}&nbsp;
            🛤지역2 : {book.area2}&nbsp;
            💒지역3 : {book.area3}&nbsp;
            ✳주문수량 : {book.BOOK_CNT}&nbsp;
            😀주문자 : {book.owner_nm}&nbsp;
            ☎연락처 : {book.tel_num}
          </li>
        ))}
      </ul>

      <p>2. fetch를 활용한 비동기 방식으로 데이터 로딩하기</p> {/* (파일이 큰 경우)  */}
      <ul>
        {books.map(book => (
          <li key={book.num}>
            num : {book.num}, 
            name : {book.name}, 
            지역1 : {book.area1}, 
            지역2 : {book.area2}, 
            지역3 : {book.area3}, 
            주문수량 : {book.BOOK_CNT}, 
            주문자 : {book.owner_nm}, 
            연락처 : {book.tel_num}
          </li>
        ))}
      </ul>
    </>
  );
}

export default Bookstore;