import React, { useState } from 'react';
import './css/Tabmnu.css';

const Tabmnu3 = () => {
  // 1. useState 함수 작성과 초기값 선언
  const [activeTab, setActiveTab]= useState('Home');

  // 2. 내용을 담는 배열 객체 선언
  const tabs = [
    {title: 'Home', content: '메인'},
    {title: 'About', content: '소개'},
    {title: 'Contact', content: '문의하기'}
  ]

  return (
    <>
      <h3>Tabmnu3 - 배열 객체를 생성하여 map함수로 콘텐츠 출력하기</h3>
      <nav className='menu_list'>
        <ul>
          {tabs.map((tab) => (
            <li key={tab.title} onClick={() => setActiveTab(tab.title)} className={activeTab === tab.title ? 'act' : ''}>
              {tab.title}
            </li>
          ))}
        </ul>
      </nav>

      {/* 상태값 변수인 activeTab이 Home, About, Contact일 경우에만 해당 내용이 출력되도록 함. */}
      <div className='content'>
        {activeTab === 'Home' ? tabs[0].content : null}
        {activeTab === 'About' ? tabs[1].content : null}
        {activeTab === 'Contact' ? tabs[2].content : null}
      </div>
    </>
  );
}

export default Tabmnu3;