import React from 'react';
import Form1 from './Form1';
import Form2 from './Form2';
import Form3 from './Form3';
import Form4 from './Form4';
import Form5 from './Form5';
import Form6 from './Form6';

function Form(props) {
  return (
    <>
      <h3 className="title3">React Form</h3>
      <p>HTML에서 폼 태그와 마찬가지로 React에서도 사용자가 웹 페이지에서 상호작용(Interaction)을 할 수 있도록 할 수 있다.</p>
      <ul>
        <li>nameChange : 이름 변경을 위한 함수명</li>
        <li>e.target.value : 이벤트 객체의 매개변수 e의 값</li>
        <li>state : 자기 자신의 콤포넌트에서 변경이 되는 값을 저장하기 위한 곳</li>
        <li>setState() 함수 : state값을 변경하기 위한 함수</li>
      </ul>

      {/* 0501 수업 */}
      <Form1 />
      <hr />
      <Form2 />

      {/* 0502 수업 */}
      <hr />
      <Form3 />
      <hr />
      <Form4 />
      <hr />
      <Form5 />
      <hr />
      <Form6 />
    </>
  );
}

export default Form;