import React, {useState} from 'react';

function UseState(props) {
  // CSS 적용을 위한 객체
  const style = {
    width: '300px',
    padding: '10px',
    margin: '10px',
    border: '3px solid #f00'
  }
  const button = {
    border: '1px solid #f00',
    background: '#e41521',
    color: '#fff',
    padding: '5px 20px',
    borderRadius: '7px',
    fontSize: '14px',
    cursor: 'pointer',
    // 강사 - padding: '3px 10px', fontSize: '20px', margin: '0px 4px'
  }

  // 상태변화를 위한 함수
  // num(상태 변수명), setNum(상태 변경을 위한 함수명)
  const [num, setNum] = useState(1); // 초기값 1 //수량
  const price = 42750; // 제품 1개 금액 //기본가격 (특정 상품 단가)
  const total = price * num; // 총 금액

  // 1씩 증가
  const onIncrease = () => {
    setNum(num + 1); // 1씩 더한다.
    // JS : num = num + 1; or num++
  }

  // 1씩 감소
  const onDecrease = () => {
    if(num === 1) { // num이 1일때
      // num = 1; // 1로 설정하고 // 이렇게 쓰면 '경고'가 뜬다. state 함수로 사용해야.
      setNum(1);
    } else { // 그렇지 않으면
      setNum(num - 1); // 1씩 감소한다.
    }
  }

  return (
    <>
      <h3 className='title2'>useState()함수를 통한 데이터 관리</h3>
      <p>클래스형 콤포넌트는 state값을 바로 사용 가능했다. 하짐나 함수형 콤포넌트에서는 state값을 사용할 수 없었는데, 리액트 버전 16.8 이상부터는 useState()함수가 추가되어 함수형 콤포넌트에서도 state값을 사용할 수 있게 되었다.</p>
      <p>state는 자신 컴포넌트가 '유동적으로 변경될 값'을 관리할 때 사용한다.</p> {/* 변수 관리 */}
      <p>useState() 함수 사용시 import, React, &#123;useState&#125; 함수를 추가해야 한다.</p> {/* {useState} */}

      <pre>
        const [값 변수, 값 변경을 위한 함수] = useState(기본값);
        const 값 변경을 위한 함수 = (); //내용
        const 스테이트 값을 갖고 오는 변수 = useState(기본값);
        const 스테이트 값 변수 = 스테이트 갖고 오는 변수[0];
        const 변경될 스테이트 값 변수 = 스테이트 갖고 오는 변수[1];
      </pre>
      <div style={style}>
        <p>값 변화 : {num}</p>
        <input type='button' value='-' onClick={onDecrease} style={button} />
        <input type='button' value='+' onClick={onIncrease} style={button} />
        {/* <p>상품가격 : 7,300원</p> // 초기 기입 */}
        <p>상품가격 : 42,750원</p>
        <p>주문가격 : {total}원</p>
      </div>
    </>
  );
}

export default UseState;