import React, { useState } from 'react';
import './css/Tabmnu.css';

const Tabmnu2 = () => {
  // const [변수명, 함수명] = useState();
  const [menuNum, setMenuNum] = useState(1);

  return (
    <>
      {/* <h3>Tabmnu 구현하기</h3> */}
      <h3>Tabmnu 구현하기(map함수를 사용하여 간소화한다.)</h3>
      <nav className='menu_list'>
        <ul>
          {/* 반복되는 부분은 배열을 사용하여 반복해 돌린다. */}
          {[1, 2, 3, 4].map((num) => (
            <li key={num} onClick={() => setMenuNum(num)} className={menuNum === num ? 'act' : ''}>{`Menu_${num}`}</li>
          ))}
        </ul>
      </nav>

      {/* 콘텐츠 1, 2, 3, 4 내용 출력하기 */}
      <div className='tab_content'>
        <div className='content'>{`Menu Contents${menuNum}`}</div>
      </div>
    </>
  );
}

export default Tabmnu2;