import React, {useState} from 'react';

function LoginForm(props) {
  // 1. 상태값 초기화
  //상태값 하나일 때
  // const [username, setName] = useState('');
  // const [userid, setId] = useState('');

  // 상태 값이 여러 개일 경우 아래 방법을 사용
  const [inputs, setInputs] = useState({
    username: '',
    userid: ''
  });

  // 2. 값을 입력받아 내용을 담는다.
  const {username, userid} = inputs;

  // 3. 함수를 작성한다.
  const onDataChange = (e) => {
    const {value, name} = e.target;
    setInputs({
      ...inputs, //기존 input 객체를 복사
      [name]: value //name 키를 가진 값을 value로 설정
      // ...객체명 - 스프레드 연산자(...), 기존 객체를 복사하여 새로운 객체에 추가하기
    })
  }

  // 데이터 초기화
  const onDataReset = (e) => {
    setInputs({
      username: '',
      userid: ''
    });
  }

  return (
    <p>
      <h3>useState 함수를 활용하여 입력폼 '이름', '아이디' 입력하면 아래 내용 출력하고 '초기화' 메뉴 클릭시 내용 비우기</h3>
      <ul>
        <li>여러 개의 input 요소를 사용시 사용될 속성 name, value 설정</li>
        <li>e.target.value //이벤트를 받는 타겟의 value값</li>
        <li>e.target.name //이벤트를 받는 타겟의 name값</li>
      </ul>

      <filedset>
        <legend>폼 그룹</legend>
        <p>
          <input type='text' placeholder='이름을 입력하세요' name='username' value={username} onChange={onDataChange} />
        </p>
        <p>
          <input type='text' placeholder='아이디를 입력하세요' name='userid' value={userid} onChange={onDataChange} />
        </p>
        <input type='button' value='데이터 초기화' onClick={onDataReset} />

        <p>값 출력하기 : {username}:({userid})</p>
      </filedset>

      <h3>보조 설명</h3>
      <p>const [inputs, setInputs] = useState; //값이 1개일 경우 useState() 함수에 값을 그냥 적으면 기본값이 들어간다.</p>
      <p>&#47;&#47; 하지만 상태값을 관리하기 위한 값이 2개이기 때문에 2개를 관리하기 위해 따로 설정함.</p>
      <p>&#47;&#47; 그럴 때는 useState() 함수에 객체로 입력하면 된다.</p>
      <br />

      <p>객체의 속성으로 값을 호출하는 방법은 아래와 같다.</p>
      <p>&#47;&#47; inputs.username;</p>
      <p>&#47;&#47; inputs.userid;</p>
      <br />

      <p>&#47;&#47; 객체 비구조화 할당 방법으로 처리하기</p>
      <p>const username, userid = inputs;</p>
      <br />

      <p>&#47;&#47; 할당 처리 후 호출하는 방법</p>
      <p>username;</p>
      <p>userid;</p>
      <p>&#47;&#47; 이렇게 작성하면 표현식으로 호출시 변수명으로 적으면 되니 간단해짐.</p>
      <br />

      <p>const &#123;value, name&#125; = e.target;</p>
      <p>&#47;&#47; 위 수식은 e.target에서 name,value를 추출하여 각각의 변수에 담는다. = 비구조화 할당방식</p>
      <br />

      <p>setInputs(&#123;</p>
        <p>...inputs,</p>
        <p>[name] : value</p>
      <p>&#125;)</p>
      <br />

      <p>&#47;&#47; 기존 inputs값을 복사해오라는 의미로 나머지 패턴 ...을 사용한다.</p>
      <p>이 때 객체의 모든 내용을 모두 펼쳐서 복사해준다.</p>
      <br />

      <p>&#47;&#47; [name] : value에서 대괄호를 쓴 것은 안 쓰고 name:value로 쓰면 기존 상태는 제거가 된다.</p>
      <p>객체 안에서 key[]로 감싸면 그 안에 넣은 속성명이 가리키는 실제 값은 key 값으로 사용된다.</p>
      <br />

      <p>&#47;&#47; 객체에 값을 할당할 때 동적인 키 값으로 값을 할당할 때 대괄호를 사용한다.</p>
    </p>
  );
}

export default LoginForm;