import React from 'react';
import dummy from '../data/noodle.json';

function Noodles(props) {
  return (
    <>
      <h3>Noodles</h3>
      <ul>
        {dummy.map(noodle => (
          <li key={noodle.num}>
            {noodle.NUM}&nbsp;
            {noodle.NAME}&nbsp;
            {noodle.COMPANY}&nbsp;
            {noodle.KIND}&nbsp;
            {noodle.PRICE}&nbsp;
            {noodle.E_DATE}
          </li>
        ))}
      </ul>
    </>
  );
}

export default Noodles;