import React from 'react';

function StudentInput({name, age, email, onCreate, onDataChange}) {
  const input_style = {
    height: "30px",
    margin: "3px"
  }
  const button_style = {
    border: "none",
    background: "#333",
    color: "#fff",
    width: "120px",
    height: "34px"
  }
  const border_style = {
    border: "3px solid #333",
    padding: "10px"
  }

  return (
    <div style={border_style}>
      <input type='text' name='name' placeholder='이름을 입력하세요' style={input_style} value={name} onChange={onDataChange} />
      <input type='text' name='age' placeholder='나이를 입력하세요' style={input_style} value={age} onChange={onDataChange} />
      <input type='text' name='email' placeholder='이메일 주소를 입력하세요 id@doamin.co.kr or com' style={input_style} value={email} onChange={onDataChange} />

      <button type='button' style={button_style} onClick={onCreate}>데이터 입력</button>
    </div>
  );
}

export default StudentInput;