import React, {useState} from 'react';

function Form4(props) {
  // const [name, setName] = useState('');
  // const [id_txt, setId] = useState('');

  const [formData, setFormData] = useState({ // 여러개의 값 관리: {}안에 입력
    name: '',
    id_txt: ''
  });
  
  // name값을 저장하기 위한 함수
  // const nameChange = (e) => {
  //   setName(e.target.value);
  // }

  // // id_txt값을 저장하기 위한 함수
  // const idTxtChange = (e) => {
  //   setId(e.target.value);
  // }

  const nameChange = (e) => {
    const {name, value} = e.target;
    setFormData((prev) => ({
      ...prev, // ES6 스프레드 연산자
      [name]:value
    }));
  }

  // 데이터 삭제를 위한 함수
  // const onDelete = (e) => {
    // setName('');
    // setId('');
  // }
  const onDelete = () => {
    setFormData({
      name: '',
      id_txt: ''
    })
  };


  return (
    <>
      <h4>4. 여러 개의 input 태그의 데이터 관리하기</h4>
      <ul>
        <li>e.target.value : 값에 접근하기</li>
        <li>e.target.name : name 속성값에 접근하기</li>
        {/* <li>this.setState({`[변수명]:값`}) : 속성명에 변수값을 사용하려면 대문자로 표시해야 함</li>
        <li>nameChange : 값을 입력하였을 때 발생되는 이벤트 함수</li> */}

        <li>state를 사용하여 입력값 관리</li>
        <li>onChange 이벤트를 통해 상태 업데이트</li>
        <li>useState 함수를 사용하여 상태관리값을 한 꺼번에 선언하고 관리할 수 있다.</li>
      </ul>

      <form >
        <p>출력내용(이름) : </p>
        <input type="text" id="name" name="name" 
        // value={name} 
        value={formData.name}
        onChange={nameChange} 
        />

        <p>출력내용(아이디) : </p>
        <input type="text" id="id" name="id_txt" 
        // value={id_txt} 
        value={formData.id_txt}
        // onChange={idTxtChange} 
        onChange={nameChange}
        />

        {/* <p>입력하신 이름은 '{name}'이고, 아이디는 '{id_txt}'입니다.</p> */}
        <p>입력하신 이름은 '{formData.name}'이고, 아이디는 '{formData.id_txt}'입니다.</p>

        <input type="button" value="다시쓰기" onClick={onDelete} />
      </form>
    </>
  );
}

export default Form4;