import React, { useState } from 'react';
import './css/Tabmnu.css';

const Tabmnu4 = () => {
  // 1. useState 함수 작성과 초기값 선언
  const [activeTab, setActiveTab]= useState('Home');

  // 2. 내용을 담는 배열 객체 선언
  const tabs = [
    {title: 'Home', content: '메인'},
    {title: 'About', content: '소개'},
    {title: 'Contact', content: '문의하기'}
  ]

  // 3. 상태값과 일치하는 제목과 내용을 찾아서 변수에 담는다.
  const activeTabContent = tabs.find((tab) => tab.title === activeTab)?.content;

  return (
    <>
      <h3>Tabmnu4 - 최적화</h3>
      <nav className='menu_list'>
        <ul>
          {tabs.map((tab) => (
            <li key={tab.title} onClick={() => setActiveTab(tab.title)} className={activeTab === tab.title ? 'act' : ''}>
              {tab.title}
            </li>
          ))}
        </ul>
      </nav>

      {/* 상태값 변수가 Home이면 해당 내용이 출력되도록 함. */}
      <div className='content'>
        {activeTab}
      </div>
      {activeTabContent}
    </>
  );
}

export default Tabmnu4;