import React from 'react';
import dummy from '../data/customer.json';

function Customer(props) {
  return (
    <>
      <h3>Customer json 데이터 출력하기</h3>
      <ul>
        {dummy.map(customer => (
          <li key={customer.id}>
            레스토랑명 : {customer.c_name}&nbsp;
            주소 : {customer.c_address}&nbsp;
            연락처 : {customer.c_tel}
          </li>
        ))}
      </ul>
    </>
  );
}

export default Customer;