  useEffect(() => {
    const handleScroll = () => {
      setVisible(window.scrollY > 200); // 윈도우 스크롤 값이 200 초과일 때 top버튼이 보이게...
    }
    // 스크롤 이벤트를 이용하여 함수 호출
    window.addEventListener('scroll', handleScroll);
    
    return () => window.addEventListener('scroll', handleScroll);
  }, []);

  // Top버튼 클릭시 실행되는 함수
  const scrollTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  }

  // 새로고침 기능 추가
  const handleRefresh = () => {
    window.location.reload(); // 윈도우 주소값을 다시 불러옴.
  }

  // 메뉴 클릭시 해당 id값을 찾아서 이동하도록 한다.
  const handleMenuClick = (id) => {
    const el = document.getElementById(id);
    if(el) {
      const y = el.getBoundingClientRect().top + window.pageYOffset - 58;
      window.scrollTo({top: y, behavior: 'smooth'});
      setMenuOpen(false);
    }
  };

