import React from 'react';

function Student({student, onRemove}) {
  return (
    <>
      {/* <p>이름 : {student.name} </p>
      <p>나이 : {student.age} </p>
      <p>이메일 : {student.email} </p> */}

      <div>
        <strong>이름 : {student.name} </strong>
        <strong>나이 : {student.age} </strong>
        <strong>이메일 : {student.email} </strong>

        <button type='button' onClick={() => onRemove(student.id)}>데이터 삭제</button>
      </div>
      <hr />
    </>
  );
}

export default Student;