import React from 'react';
import Bookstore from './Bookstore';
import Customer from './Customer';
import Fruit from './Fruit';
import Goods from './Goods';
import Noodle from './Noodle';
import Orders from './Orders';

function Json(props) {
  return (
    <>
      <h2 className='title2'>9일차 - DB에서 저장한 JSON 데이터 map함수로 출력하기, Hooks(useEffect)</h2>
      <p>JSON(제이슨, Javascript Object Notation)은 속성-값-쌍(attribute-value pairs and array data types (or any other serializable value)) 또는 "키-값 쌍"으로 이루어진 데이터 오브젝트를 전달하기 위해 인간이 읽을 수 있는 텍스트를 사용하는 개방형 표준 포맷</p>
      <p>비동기 브라우저/서버 통신(AJAX)을 위해, 넓게는 XML(AJAX가 사용)을 대체하는 주요 데이터 포맷이다. 특히 인터넷에서 자료를 주고받을 때 그 자료를 표현하는 방법으로 알려져 있다.</p>
      <p>자료의 종류에 큰 제한은 없으며, 특히 컴퓨터 프로그램의 변수값을 표현하는데 적합하다.</p>
      <p>본래는 자바스크립트 언어로부터 파생되어 자바스크립트의 구문 형식을 따르지만 언어 독립형 데이터 포맷이다.</p>
      <p>프로그래밍 언어나 플랫폼에 독립적이므로, 구문 분석 및 json 데이터 생성을 위한 코드는 C, C++, C#, 자바, 자바스크립트, 펄, 파이썬 등 수많은 프로그래밍 언어에서 쉽게 이용할 수 있다.</p>
      <p>파일의 확장자는 .json이다.</p>

      <dl>
        <dt>json 데이터를 불러와 map함수로 출력하기</dt>
        <dd>1. BOOK_STORE</dd>
        <dd>2. CUSTOMER</dd>
        <dd>3. GOODS</dd>
        <dd>4. NOODLE</dd>
        <dd>5. ORDERS</dd>
        <dd>6. FRUIT</dd>
      </dl>

      <hr />
      <Bookstore /><hr />
      <Customer /><hr />
      <Fruit /><hr />
      <Goods /><hr />
      <Noodle /><hr />
      <Orders />
    </>
  );
}

export default Json;