import React, { useState } from 'react';

function Modal(props) {
  // const [변수명, 함수명] = useState(초기값);
  const [showModal, setShowModal] = useState(false); // false - 버튼 클릭 시 나오게 하는 상태값
  // true 미리 나와있게 함. | false 경우 버튼 클릭시 나옴

  return (
    // 상태값 true면 나오고, false는 숨김
    <>
      <button onClick={() => setShowModal(true)}>모달 열기</button>
      
      {/* 조건부 렌더링 공식
        {조건 && (... : 조건에 따른)}
       */}
      {showModal && (
        <div style={{position: 'fixed', top: 0, left: 0, right: 0, bottom: 0, backgroundColor: 'rgba(0,0,0,0.5)', display: 'flex', justifyContent: 'center', alignItems: 'center', zIndex: 1000}}>
          <div style={{width: '400px', height: '500px', backgroundColor: '#fff', position: 'fixed', top: '50%', left: '50%', transform: ('translate(-50%, -50%)'), textAlign: 'center'}}>
            <img src={`${process.env.PUBLIC_URL}/images/coupon.jpg`} alt='모달 이미지' style={{width: '100%'}} />
            <button onClick={() => setShowModal(false)} style={{width: '100%', height: '50px', backgroundColor: '#fff', color: '#333', border: 'none'}}>닫기</button>
          </div>
        </div>
      )}
    </>
  );
}

export default Modal;