import React from 'react';
import dummy from '../data/goods.json';

function Goods(props) {
  return (
    <>
      <h3>goods</h3>
      <ul>
        {dummy.map(goods => (
          <li key={goods.g_code}>
            {goods.g_code}&nbsp;
            {goods.g_name}&nbsp;
            {goods.g_cost}
          </li>
        ))}
      </ul>
    </>
  );
}

export default Goods;