import React from 'react';
import musicData from './music_data.json';

function Music(props) {
  const fruites = ['사과', '바나나', '오렌지'];

  return (
    <>
      <p>map함수 - 기존 배열을 이용하여 새로운 배열을 만들고자 할 때 사용.</p>
      {/* <pre>
        // 1. 배열 객체 생성
        const fruites = ['사과', '바나나', '오렌지']; <br />
        // 2. 함수 안에 map 함수로 배열 데이터 출력
        <ul>
          {fruites.map((fruit, index) => {
            <li key={index}>{fruit}</li>
          })}
        </ul>
      </pre> */}

      <ul>
        {fruites.map((fruit, index) => (
          <li key={index}>{fruit}</li>
        ))}
      </ul>

      <ul>
        {musicData.map((music, i) => (
          <li key={music.id}>
            {/* {music.title} */}
            <ul>
              <li>{music.id}</li>
              <li>{music.title}</li>
              <li>{music.url}</li>
              <li>{music.thumbnailUrl}</li>
            </ul>
          </li>
        ))}
      </ul>
    </>
  );
}

export default Music;