import React, {useState, useRef} from 'react';
import './ref.css';

function UseRef(props) {
  // 1. state 선언
  const [inputs, setInputs] = useState({
    password: '', // 패스워드 초기값
    clicked: false, // 클릭했을 때 초기값 false
    validated: false // 유효성검사 초기값 false
  });
  
  // 2. 비구조화 할당방식
  const {password, clicked, validated} = inputs;

  // 3. uesRef 선언
  const passwordInput = useRef();

  // 4. 입력박스에 사용자가 패스워드를 입력하는 경우 실행하기 위한 함수
  const onInputChange = (e) => {
    setInputs({
      password: e.target.value // 입력한 패스워드를 변수에 담기
    });
  }

  // 5. 버튼 클릭시 패스워드를 검증하기 위한 함수
  const onInputCheck = (e) => {
    e.preventDefault(); // 기본 이벤트를 제거하고
    setInputs({
      clicked: true, // 클릭시 상태값이 true로 전환
      validated: password === '1234' // 입력된 패스워드 값이 일치하는지 여부를 비교한다.
    });

    if(password !== '1234') {
      passwordInput.current.focus(); //초점 이동하기
    }
  }

  // 패스워드 초기화 버튼 클릭시 실행되는 함수
  const onClear = (e) => {
    setInputs({
      password: ''
    });
  }

  return (
    <>
      <h3>Ref, useRef</h3>
      <p>ref는 '개발자가 특정 DOM을 선택해야 하는 상황이 발생될 때 사용하는 기능'으로 클래스 컴포넌트에서 사용하지만 hooks에서 useRef 함수가 추가되면서 함수형 콤포넌트에서도 사용이 가능하다.</p>
      <p>Javascript : DOM 선택시 getElemnetById(), querySelector()</p>
      <p>jQuery : DOM 선택시 $('선택자')</p>
      <p>React의 JSX 안에서 DOM에 id를 달면 해당 DOM을 렌더링할 때 그대로 전달된다.</p>
      <p>컴포넌트가 여러 개 생성되는 경우 같은 id가 중복되어 에러가 발생될 수 있다. 하지만 ref는 전역적으로 작동하지 않고, 콤포넌트 내부에서만 작동하기 때문에 이런 문제가 발생되지 않는다.</p>

      <dl>
        <dt>DOM을 꼭 사용해야 하는 상황</dt>
        <dd>특정 input 태그에 포커스 추가</dd>
        <dd>스크롤 박스를 조작하기</dd>
        <dd>Canvas에 그림 그리기</dd>
        <dd>기타 ...</dd>
      </dl>

      <h3>실습. 입력 양식을 만들고 버튼 클릭시 초점 처리하기</h3>
      <p>입력 양식에 사용자가 내용을 입력하여 내용이 일치하면 '초록색' 배경, 그렇지 않다면 '회색' 배경으로 변경되도록 하기</p>
      {/* 초기입력: 일치하면 '초록색' 배경, 그렇지 않다면 '주황색' 배경 */}
      <dl>
        <dt>ref, useRef를 사용하지 않는 폼 태그의 문제점</dt>
        <dd>사용자가 클릭하고 값을 입력한 경우 아이디나 패스워드가 일치하면 상관없지만 틀린 경우는 다시 입력할 수 있도록 초점을 이동해야 한다.</dd>
        <dd>그래서 특정 DOM을 선택하기 위해 ref, useRef 함수를 사용하여 초점이 정확하게 맞춰지도록 해야 함.</dd>
      </dl>

      <form name='' method='' action='' className='formStyle'>
        <fieldset>
          <legend>폼 태그 제목</legend>
          <p><input type='password' value={password} onChange={onInputChange} ref={passwordInput} className={clicked && (validated ? 'success': 'failure')} /></p>
          <p>
            <input type='button' value='검증하기' onClick={onInputCheck} />

            <input type='button' value='패스워드 초기화' onClick={onClear} />
            </p>
        </fieldset>
      </form>

      <hr />
      <p>현재 입력값 : {password}</p>

      <p>패스워드 초기화 버튼 클릭시 입력된 패스워드 삭제하기(초기화하기)</p>
    </>
  );
}

export default UseRef;