import React, {useState, useRef} from 'react';
import Student from './Student'; //자식 컴포넌트 연결
import StudentInput from './StudentInput';

function StudentList(props) {
  /* 배열 데이터 목록 출력하기 */
  const [students, setStudents] = useState([
    {
      id: 'st001',
      name: '김남준',
      age: 28,
      email: 'rm@gmail.com'
    },
    {
      id: 'st002',
      name: '김석진',
      age: 30,
      email: 'kim@gmail.com'
    },
    {
      id: 'st003',
      name: '최명길',
      age: 33,
      email: 'choigil@gmail.com'
    },
    {
      id: 'st004',
      name: '오수정',
      age: 21,
      email: 'ohsujin@gmail.com'
    },
    {
      id: 'st005',
      name: '이미자',
      age: 30,
      email: 'imij@gmail.com'
    },
  ]
  );

  // 데이터 개수 5개가 존재하기 때문에 초기값은 6이 되어야
  const nextId = useRef(6);

  // 입력상자에서 사용할 값을 state 관리 [상태관리, 초기값]
  const [inputs, setInputs] = useState({
    name: '',
    age: '',
    email: ''
  });
  
  // 비구조화 할당 방식으로 state값을 각각 변수에 담기
  const {name, age, email} = inputs;

  // 1. 데이터 변경 함수
  const onDataChange = (e) => {
    // name과 value는 값이 변경되는 input 태그의 속성을 비구조화 할당한다.
    const {name, value} = e.target;

    // state값 변경
    setInputs({
      ...inputs, //기존 데이터 배열에 새롭게 데이터를 추가
      [name]: value
    });
  };

  // 2. 데이터 추가 함수
  const onCreate = () => {
    // 유효성 검사 실시
    if(!name.trim() || !email.trim() || !age) { //공백제거 trim
      alert('이름, 나이, 이메일 주소를 모두 입력해주세요.');
      return;
    }

    // 데이터 추가하는 구문
    const student = { //자식에게 전달되는 값 student
      id: 'st00' + nextId.current,
      name,
      age: parseInt(age, 10), //정수형으로 입력
      email
    };

    //담기
    // 스프레드 연산자(...) - 기존 students 배열에 새로운 값을 이어서 추가한다.
    // 새로 추가 입력한다.
    setStudents([...students, student]);
    // 입력되고 나면 상태 변수값은 다시 초기화한다.
    setInputs({
      name: '',
      age: '',
      email: ''
    });

    // id값은 1씩 증가시켜 준다.
    nextId.current += 1; // = (nextId.current = nextId.current + 1;)
  }

  // 3. 데이터 삭제 함수
  // 사용자가 클릭한 자료의 id값을 찾아서 데이터 삭제하고, 다시 새로운 배열을 만들어야 함.
  const onRemove = (id) => {
    // student.id가 id와 같은 데이터를 삭제해야
    setStudents(students.filter((student) => student.id !== id));
  }

  return (
    <>
      {/* 배열 데이터 입력 폼 만들기 */}
      <StudentInput 
        name={name} 
        age={age}
        email={email}
        onCreate={onCreate}
        onDataChange={onDataChange}
      />

      {/* 출력 - 방법3 */}
      {/* 방법1. 비효율적인 배열 데이터 가져오기
      <ul>
        <li>
          <p>이름 : {students[0].name}</p>
          <p>나이 : {students[0].age}세</p>
          <p>이메일 : {students[0].email}</p>
        </li>
        <li>
          <p>이름 : {students[1].name}</p>
          <p>나이 : {students[1].age}세</p>
          <p>이메일 : {students[1].email}</p>
        </li>
        <li>
          <p>이름 : {students[2].name}</p>
          <p>나이 : {students[2].age}세</p>
          <p>이메일 : {students[2].email}</p>
        </li>
      </ul> */}
      
      {/* 방법2. 자식 콤포넌트에 props값을 전달하여 자식 콤포넌트에서 출력하도록 함.
      <ul>
        <li><Student student={students[0]} /></li>
        <li><Student student={students[1]} /></li>
        <li><Student student={students[2]} /></li>
        <li><Student student={students[3]} /></li>
        <li><Student student={students[4]} /></li>
      </ul> */}

      {/* 방법3. map함수로 데이터를 반복 출력
        - map 함수는 기존 배열을 가지고 새로운 배열을 생성할 때 사용
        - map((현재 배열값, 배열 내 현재값 인덱스, 현재 배열), 함수 내에서 this로 사용될 값)
        - array.map(function(currentValue, index, array){새로운 배열}, thisArg)
        - 배열 데이터에서 중복되지 않는 값에 key값을 설정해야 함.
      */}

      {/* 배열 객체에서 고유한 값이 있는 경우는 아래처럼 작성 */}
      {students.map(student => (
        <Student student={student} key={student.id} onRemove={onRemove} />
      ))}

      {/* 배열 객체에서 고유한 값이 없는 경우는 아래처럼 작성 */}
      {/* {students.map((student, index) => (
        <Student student={student} key={index} onRemove={onRemove} />
      ))} */}
    </>
  );
}

export default StudentList;